import ChipDisplay from "./ChipDisplay";
import HoleCards from "./HoleCards";
import PlayerBalance, { PlayerBankroll } from "./PlayerBalance";
import PlayerBoxContainer from "./PlayerBoxContainer";
import PlayerInfo from "./PlayerInfo";
import PlayerName from "./PlayerName";

export default function PlayerBox({
  balance,
  playerName,
  amountBet,
  cards,
  bankroll,
  highlight,
}) {
  return (
    <PlayerBoxContainer>
      <HoleCards cards={cards} />
      <ChipDisplay visible={amountBet != "$0.00"}>{amountBet}</ChipDisplay>
      <PlayerInfo highlight={highlight}>
        <PlayerName highlight={highlight}>{playerName}</PlayerName>
        <PlayerBalance highlight={highlight}>
          {balance}{" "}
          <PlayerBankroll highlight={highlight}>({bankroll})</PlayerBankroll>
        </PlayerBalance>
      </PlayerInfo>
    </PlayerBoxContainer>
  );
}
