import { useEffect, useState } from "react";

import PlayerBox from "./components/PlayerBox/PlayerBox";
import Board from "./components/Board/Board";
import ActionInput from "./components/ActionInput/ActionInput";
import BoardContainer from "./components/Board/BoardContainer";

export default function GameManager({ socket }) {
  // State of the current player
  const [playerState, setPlayerState] = useState({
    bankroll: 0,
    stack: 0,
    amountBet: 0,
    holeCards: ["XX", "XX"],
    highlight: false,
  });

  // State of the opponent player
  const [opponentState, setOpponentState] = useState({
    bankroll: 0,
    stack: 0,
    amountBet: 0,
    holeCards: ["XX", "XX"],
    highlight: false,
  });

  // State of the overall game
  const [gameState, setGameState] = useState({
    street: "PREFLOP",
    boardCards: [],
    potSize: 0,
  });

  // Keep track of if the game state needs to be received from the server
  const [receivedState, setReceivedState] = useState(false);

  // Keep track of what actions are allowed
  const [allowedActions, setAllowedActions] = useState([]);

  useEffect(() => {
    // First attempt to receive state if it does not exist
    const checkState = setInterval(() => {
      if (!receivedState) {
        socket.emit("playground_request_refresh");
      }
    }, 500);

    // Handle the occurrence of a new hand
    socket.on("playground_new_round_state", (state) => {
      clearInterval(checkState);
      setReceivedState(true);

      console.log("[SOCKET] New hand state received");

      // // Reset the state of the game
      // setPlayerState({
      //   bankroll: state.my_bankroll,
      //   stack: 0,
      //   amountBet: 0,
      //   holeCards: state.my_cards.map((c) => c.toUpperCase()),
      //   highlight: false,
      // });

      // setOpponentState({
      //   bankroll: -state.my_bankroll,
      //   stack: 0,
      //   amountBet: 0,
      //   holeCards: ["XX", "XX"],
      //   highlight: false,
      // });

      // setGameState({
      //   street: "PREFLOP",
      //   boardCards: [],
      //   potSize: 0,
      // });

      setAllowedActions([]);
    });

    socket.on("playground_update_round_state", (state) => {
      clearInterval(checkState);
      setReceivedState(true);

      console.log("[SOCKET] Hand update received", state);

      setPlayerState({
        bankroll: state.my_bankroll,
        stack: state.my_stack,
        amountBet: state.my_pip,
        holeCards: state.my_cards.map((c) => c.toUpperCase()),
        highlight: false,
      });

      setOpponentState({
        bankroll: -state.my_bankroll,
        stack: state.opp_stack,
        amountBet: state.opp_pip,
        holeCards: ["XX", "XX"],
        highlight: false,
      });

      const streetNames = {
        [0]: "PREFLOP",
        [3]: "FLOP",
        [4]: "TURN",
        [5]: "RIVER",
      };

      setGameState({
        street: streetNames[state.street],
        boardCards: state.board_cards,
        potSize: state.pot_size,
      });

      setAllowedActions(state.legal_actions_list);
    });

    socket.on("playground_end_round_state", (state) => {
      clearInterval(checkState);
      setReceivedState(true);

      console.log(
        "[SOCKET] Round end received",
        state,
        playerState,
        opponentState
      );

      const newOppState = {
        ...opponentState,
        // bankroll: -state.my_bankroll,
        // stack: state.opp_stack,
        amountBet: 0,
        holeCards: state.opp_cards.map((c) => c.toUpperCase()),
        highlight: state.my_delta <= 0,
      };

      const newPlayerState = {
        ...playerState,
        // bankroll: state.my_bankroll,
        // stack: state.my_stack,
        holeCards: state.my_cards.map((c) => c.toUpperCase()),
        amountBet: 0,
        highlight: state.my_delta >= 0,
      };

      setOpponentState(newOppState);
      setPlayerState(newPlayerState);

      console.log(newOppState, newPlayerState);

      setAllowedActions([]);
    });

    return () => clearInterval(checkState);
  }, [socket]);

  const callHandler = () => {
    socket.emit("playground_act_call");
  };

  const checkHandler = () => {
    socket.emit("playground_act_check");
  };

  const raiseHandler = (amount) => {
    console.log("working", amount);
    socket.emit("playground_act_raise", { amount: amount });
  };

  const foldHandler = () => {
    socket.emit("playground_act_fold");
  };

  return (
    <div className="game-container">
      <PlayerBox
        bankroll={`$${opponentState.bankroll.toFixed(2)}`}
        balance={`$${opponentState.stack.toFixed(2)}`}
        amountBet={`$${opponentState.amountBet.toFixed(2)}`}
        playerName="Poker Bot"
        cards={opponentState.holeCards}
        highlight={opponentState.highlight}
      />
      <BoardContainer>
        <Board
          street={gameState.street}
          cards={gameState.boardCards}
          potSize={gameState.potSize}
        />
      </BoardContainer>
      <PlayerBox
        bankroll={`$${playerState.bankroll.toFixed(2)}`}
        balance={`$${playerState.stack.toFixed(2)}`}
        amountBet={`$${playerState.amountBet.toFixed(2)}`}
        playerName="Human"
        cards={playerState.holeCards}
        highlight={playerState.highlight}
      />
      <ActionInput
        legalActions={allowedActions}
        callHandler={callHandler}
        checkHandler={checkHandler}
        raiseHandler={raiseHandler}
        foldHandler={foldHandler}
      />
    </div>
  );
}
