import styled from "styled-components";

const Card = styled.div`
  border-radius: 0px;
  width: 75px;
  height: 105px;
  background-image: url(/cards/${(props) =>
    props.show ? props.card : "BLUE_BACK"}.svg);
  background-size: cover;
`;

const BoardCards = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  padding-left: 85px;
  padding-right: 85px;
  z-index: 2;

  opacity: ${(props) => (props.visible ? 1 : 0)};
`;

const Pot = styled.div`
  color: #ffffff;
  text-align: center;
  margin-bottom: 15px;
  margin-top: -30px;
`;

const PotContents = styled.div`
  display: inline-block;
  background-color: rgba(38, 38, 38, 0.15);
  border-radius: 50px;

  font-weight: bold;
  padding: 6px 65px;
`;

export default function Board({ street, cards, potSize }) {
  const flop = street === "FLOP";
  const turn = street === "TURN";
  const river = street === "RIVER";

  const boardCards = [...cards, "XX", "XX"];

  return (
    <>
      <Pot>
        <PotContents>${potSize.toFixed(2)}</PotContents>
      </Pot>
      <BoardCards visible={flop || turn || river}>
        <Card card={boardCards[0]} show={flop || turn || river} />
        <Card card={boardCards[1]} show={flop || turn || river} />
        <Card card={boardCards[2]} show={flop || turn || river} />
        <Card card={boardCards[3]} show={turn || river} />
        <Card card={boardCards[4]} show={river} />
      </BoardCards>
    </>
  );
}
