import { useEffect, useState } from "react";
import io from "socket.io-client";

import GameManager from "./GameManager";

import "./App.css";

function App() {
  const [socket, setSocket] = useState(null);
  const [connected, setConnected] = useState(false);

  useEffect(() => {
    const newSocket = io("http://127.0.0.1:2000");
    setSocket(newSocket);
    setConnected(false);

    return () => {
      setConnected(false);
      newSocket.close();
    };
  }, [setSocket, setConnected]);

  useEffect(() => {
    if (socket) {
      socket.on("connect", () => {
        socket.emit("playground_connected");
        setConnected(true);
      });

      socket.on("disconnect", () => {
        setConnected(false);
      });
    }
  }, [socket, setConnected]);

  if (!connected) {
    return (
      <div className="game-container">
        <p>Not Connected. Is the engine running?</p>
      </div>
    );
  } else {
    return <GameManager socket={socket} />;
  }
}

export default App;
