import styled from "styled-components";

const HoleCardContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const Card = styled.div`
  border-radius: 0px;
  width: 75px;
  height: 105px;
  background-image: url(/cards/${(props) =>
    props.card == "XX" ? "BLUE_BACK" : props.card}.svg);
  background-size: cover;
`;

const LeftCard = styled(Card)`
  margin-right: -10px;
  margin-bottom: -40px;
  transform: rotate(-7deg);
  z-index: -2;
`;

const RightCard = styled(Card)`
  margin-bottom: -40px;
  transform: rotate(7deg);
  z-index: -1;
`;

export default function HoleCards({ cards }) {
  if (!cards || cards.length !== 2) {
    cards = ["XX", "XX"];
  }
  return (
    <HoleCardContainer>
      <LeftCard card={cards[0]} />
      <RightCard card={cards[1]} />
    </HoleCardContainer>
  );
}
