import styled from "styled-components";
import { useState } from "react";

const ActionButton = styled.button`
  outline: none;
  text-decoration: none;
  background: transparent;
  border-style: solid;
  border-width: 0.08rem;
  border-bottom-width: 0.15rem;
  border-color: #fff;
  color: #fff;
  font-weight: bold;
  height: 3.2rem;
  border-radius: 0.3rem;
  // text-transform: uppercase;
  font-size: 1rem;
  padding: 0 1em;
  box-sizing: border-box;
  cursor: pointer;
  text-align: center;

  padding: 0 1.2rem;
  height: 3.5rem;
  margin-left: 0.5em;
  font-size: 0.8rem;

  color: ${(props) => (props.fold ? "#fc4117" : "#53d78b")};
  border-color: ${(props) => (props.fold ? "#fc4117" : "#53d78b")};

  opacity: ${(props) => (props.disabled ? 0.3 : 1)};

  &:active {
    background-color: ${(props) => (props.fold ? "#fc4117" : "#53d78b")};
    color: white;
  }
`;

const ActionInputContainer = styled.div`
  display: block;
  margin-top: 30px;

  opacity: 0.3;

  &:hover {
    opacity: 1;
  }
`;

const RaiseInputContainer = styled.div`
  vertical-align: top;
  position: relative;
  // background: #2f2f2f;
  color: #000;
  height: 100%;
  width: 9em;
  overflow: hidden;
  padding: 0;
  box-sizing: border-box;
  border-radius: 0.5em;
  display: inline-block;
`;

const RaiseValueInputContainer = styled.div`
  margin-top: 0.3rem;
  margin-left: 2.2%;
  margin-bottom: 0.3rem;
  width: 95%;
  position: relative;
`;

const RaiseInput = styled.input`
  border-radius: 0.3rem;
  font-size: 1.8em;
  font-weight: bold;
  color: #fff;
  width: 100%;
  background: rgba(255, 255, 255, 0.04);
  padding: 0;
  border: 0.05rem solid rgba(255, 255, 255, 0.1);
  text-align: center;

  &:focus {
    outline: none;
  }
`;

const BetLabel = styled.p`
  color: gray;
  font-size: 0.8em;
  text-align: center;
  margin: 0;
`;

const RaiseControllerContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 0.75rem;
`;

export default function ActionInput({
  legalActions,
  callHandler,
  checkHandler,
  foldHandler,
  raiseHandler,
}) {
  const [raiseAmount, setRaiseAmount] = useState(0.0);

  const updateRaiseAmount = (e) => {
    setRaiseAmount(e.target.value);
  };

  return (
    <ActionInputContainer>
      <ActionButton
        disabled={!legalActions.includes("check")}
        onClick={checkHandler}
      >
        Check
      </ActionButton>
      <ActionButton
        disabled={!legalActions.includes("call")}
        onClick={callHandler}
      >
        Call
      </ActionButton>
      {/* <ActionButton disabled={!legalActions.includes("raise")}>
        Raise
      </ActionButton> */}
      <ActionButton
        fold={true}
        disabled={!legalActions.includes("fold")}
        onClick={foldHandler}
      >
        Fold
      </ActionButton>

      <RaiseControllerContainer>
        <RaiseInputContainer>
          <RaiseValueInputContainer>
            <BetLabel>Your Bet</BetLabel>
            <RaiseInput
              precision="2"
              type="text"
              className="value"
              pattern="[0-9]*"
              inputmode="numeric"
              value={raiseAmount}
              onChange={updateRaiseAmount}
            />
          </RaiseValueInputContainer>
        </RaiseInputContainer>
        <ActionButton
          disabled={!legalActions.includes("raise")}
          onClick={() => raiseHandler(raiseAmount)}
        >
          Raise
        </ActionButton>
      </RaiseControllerContainer>
    </ActionInputContainer>
  );
}
